*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.menu-style{
  border-bottom: 1px solid black;
}
.about{
  text-align: center;
text-transform: uppercase;
}
.line{
  margin:  0 auto;
}
.navbar-brand{
font-size: 30px;
color:springgreen  !important;
font-weight: bolder;
font-style: oblique;
text-transform: uppercase;
letter-spacing: 2px;
}
.submit{
  text-align: center;
}
.btn{
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.icon{
  display: flex;
  justify-content: space-between;
  margin: 0 5%;
  padding: 0 5%;
}
.add{
  display: flex;
  justify-content: space-around;

  padding: 0 5%;
}
.edit{

  padding: 0 5%;
}
.footer{
  display: flex;
  justify-content: space-between;


}
a{
  max-width: 786px;
  height: auto;
  margin: 0 20px;
  text-decoration: none;
}
.nav{

  padding: 0 150px;
}
.menu{
  padding: 0 150px;

}

.menu a{
  max-width: 786px;
  height: auto;

 text-decoration: none;
}

.nav a{
  max-width: 786px;
  height: auto;
  /* margin: 0 20px; */

text-decoration: none;
}
.nav{

  padding: 0 150px;
}
@media only screen and (max-width: 600px){
  .menu{
    display: flex;
    flex-direction: column;
    padding: 0 20px;
    justify-content: center;
    align-items: center;

  }
  .nav{
    display: flex;
    flex-direction: column;
    padding: 0 20px;
    justify-content: center;
    align-items: center;

  }

}
